.footer {
  color: $active-grey;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.1px;
  width: 100%;
  height: 129px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 50px;
  text-align: center;

  &__text {
    width: 320px; } }

@media screen and (min-width: 768px) {
  .footer {
    gap: 0 !important;
    width: 100%;
    height: 81px;
    flex-direction: row;
    margin-top: 65px;
    padding: 0 24px;
    box-sizing: border-box;
    justify-content: space-between;

    span:first-child {
      text-align: start !important; }

    span:last-child {
      width: 330px;
      text-align: end !important; } }

  .footer__text {
    width: 220px; } }

@media screen and (min-width: 880px) {
  .footer {
    margin-top: 66px;
    padding-left: 29px;
    padding-right: 35px; } }
